.fadeContainer {
  position: relative;
  width: 100%;
  height: 100px;
  margin-top: 40px;
  margin-bottom: 30px;
}

.fadeText {
  position: absolute;
  width: 100%;
  text-align: center;
  transition: opacity 1s ease-in-out;
  opacity: 0;
  color: #8f8f8f;
}

.fadeText.show {
  opacity: 1;
}
